export default {
	data(){
		return{
			categoryList:[],
			subCategoryList: [],
			height:0,
			categoryActive:0,
			categorySubActive:0,
			scrollTop:0,
			scrollHeight:0,
			repairObj:{
				bigID:'',//第一级的id
				subID:'',//第二季的id
				bigName:'',
				subName:''
			},
			list:[]

		}
	},
	methods:{
		scroll(e){
			this.scrollHeight = e.detail.scrollHeight;
		},
		categoryClickMain(categroy, index) {
			this.categoryActive = index;
			this.categorySubActive=0;//右边默认为第一个
			this.subCategoryList = categroy.subCategoryList;
			this.scrollTop = -this.scrollHeight*index;
			if(this.subCategoryList.length>0){
				this.repairObj.subID=this.subCategoryList[0].obj.id;
			}



		},
		categoryClickSub(categroy, index) {
			this.categorySubActive = index;
			this.scrollTop = -this.scrollHeight*index;
			this.repairObj.subID=this.subCategoryList[index].obj.id;
			this.list.forEach(item=>{
				if(this.repairObj.subID==item.id){
					this.repairObj.subName=item.val
				}
				if(this.subCategoryList[index].obj.parent==item.id){
					this.repairObj.bigName=item.val
				}
			})
			// uni.reLaunch({
			// 	url:'../create-order/create-order?subID='+this.repairObj.subID+"&subName="+this.repairObj.subName+"&bigName="+this.repairObj.bigName
			// })
			uni.navigateBack({
				delta:1,
				success:(res)=>{
						this.$fire.fire('repair',this.repairObj)
				}
			})

		},
		getCategory() {
			var _this=this;
			uni.request({
				url:this.$store.state.url+'Constants',
				data:{
					owner:18,
					type:this.$store.state.constants.repair_type,
					parent:-1,
					objects:1
				},
				success: (res) => {
					this.list=res.data.data
					var bigArray=[],subArray=[];
					res.data.data.forEach(item=>{
						if(item.parent==0){
							bigArray.push(item)
						}else{
							subArray.push(item);
						}
					})
					this.repairObj.subID=subArray[0].id;
					for(var i=0;i<bigArray.length;i++){
						var subList = [];
						for(var j=0;j<subArray.length;j++){
							if(subArray[j].parent==bigArray[i].id){
								subList.push({"NAME":subArray[j].val,obj:subArray[j]})
							}
						}
						this.categoryList.push({"NAME":bigArray[i].val,"subCategoryList":subList,obj:bigArray[i]})
					}
					this.categoryList[0].subCategoryList.unshift({"NAME":'维修明细类别选择',obj:{}})
					this.subCategoryList = this.categoryList[0].subCategoryList;
				}
			})

		}
	},
	onLoad:function(){
		this.getCategory()
		uni.getSystemInfo({
			success: res => {
				this.height = res.screenHeight;
			}
		})
	}
}
